import type { Metadata } from "next";
import { Geist, Geist_Mono } from "next/font/google";
import "./globals.css";
import { useEffect, useState } from "react";
import axios from "axios";
import Link from "next/link";

const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
});

export const metadata: Metadata = {
  title: "In Death Tournament",
  description: "Unofficial Tournament Platform for In Death: Unchained",
};

interface User {
  _id: string;
  username: string;
  avatar: string;
}

export default function RootLayout({
  children,
}: Readonly<{ children: React.ReactNode }>) {
  const [user, setUser] = useState<User | null>(null);
  const [dropdownOpen, setDropdownOpen] = useState(false);

  useEffect(() => {
    axios
      .get("http://localhost:5000/auth/user", { withCredentials: true })
      .then((res) => setUser(res.data))
      .catch(() => setUser(null));
  }, []);

  const handleLogout = async () => {
    await axios.get("http://localhost:5000/auth/logout", { withCredentials: true });
    setUser(null);
  };

  return (
    <html lang="en">
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased bg-[#0d1017] text-white`}>
        {/* ✅ Navbar */}
        <nav className="flex justify-between items-center px-6 py-4 bg-black shadow-lg">
          <Link href="/" className="text-2xl font-bold text-white">
            In Death Tournament
          </Link>

          {/* ✅ User Avatar & Dropdown */}
          {user && (
            <div className="relative">
              <img
                src={user.avatar}
                alt="User Avatar"
                className="w-12 h-12 rounded-full cursor-pointer border border-gray-500"
                onClick={() => setDropdownOpen(!dropdownOpen)}
              />
              {dropdownOpen && (
                <div className="absolute right-0 mt-2 w-48 bg-gray-800 shadow-lg rounded-md p-2">
                  <p className="px-4 py-2 text-center border-b border-gray-700">{user.username}</p>
                  <button
                    onClick={handleLogout}
                    className="w-full text-left px-4 py-2 text-red-500 hover:bg-gray-700 rounded-md"
                  >
                    Logout
                  </button>
                </div>
              )}
            </div>
          )}
        </nav>

        {/* ✅ Page Content */}
        <main className="p-6">{children}</main>
      </body>
    </html>
  );
}
